<template>
 <div class="helloServer">
  <p style="color:red">{{ username }}</p>
  <p style="color:blue">{{ lastname }}</p>
 </div>
</template>

<script>

import Vue from 'vue'
import VueResource from 'vue-resource'

Vue.use(VueResource)

export default {
  name: 'helloServer',
  data: {
   username : null,
   lastname : 'Mukherjee'
  },
  ready: function () {
    this.fetchData()
  },
  methods:{
    fetchData : function () {
      var vm = this
      this.$http.get('/api/user').then(function(response){
           vm.username = response.body.username
           vm.$set('username', response.body.username)
       }, function(response){ //error
        console.log(response.status)
      })
    }
  }
}

</script>


